input[type=text] {
    width: 64rem;
    padding: 1rem 1rem;
    margin: 8px 0;
    box-sizing: border-box;
    border: none;
    border-bottom: 2px solid #26547C;
    font-size: 1.25rem;
    color: #26547C;
  }

input[type=text]:focus {
    outline: none;
}

button.submit-button {
    width: 64rem;
    padding: 1rem 1rem;
    background-color: #EF5D5D;
    color: #fff;
    border: none;
    border-radius: 5px;
    font-size: 1.125rem;
    font-weight: bold;
}

button.submit-button {
    cursor: pointer;
}